













































































































import { Component, Vue, Watch } from "vue-property-decorator";
import { apiUrl } from "../../api";
import MarkDown from "./components/markDown.vue";
import moment from "moment";
import Uploads from "@/components/uploads/upload.vue";
import { Getter } from "vuex-class";

@Component({
  name: "articleAdd",
  components: {
    MarkDown,
    Uploads
  }
})
export default class ArticleAdd extends Vue {
  private ruleForm = {
    title: "",
    top: false,
    category_id: "",
    keyword: "",
    thumb_url: "",
    type: [],
    content: "",
    desc: "",
    article_author: "",
    createdAt: moment().format("YYYY-MM-DD HH:MM:ss"),
    count: 0,
    content_mark: ""
  };
  get defauleTime() {
    return "2012-12-12 12:12:12";
  }
  @Getter getArticleName: string;
  @Watch("ruleForm.content")
  changDesc(val) {
    this.ruleForm.desc = val
      .replace(/<.*?>/gi, "")
      .replace(/[\r\n]/g, "")
      .substring(0, 200);
  }
  private categoryList: Array<any> = [];
  private rules = {
    title: [
      { required: true, message: "请输入标题", trigger: "blur" },
      {
        min: 3,
        max: 50,
        message: "长度在 3 到 50 个字符",
        trigger: "blur"
      }
    ],
    count: [
      {
        type: "number",
        required: true,
        message: "请输入数字",
        trigger: "blur"
      }
    ],

    category_id: [
      {
        required: true,
        message: "请选择一个栏目",
        trigger: "change"
      }
    ],
    content: [{ required: true, message: "请输入内容", trigger: "change" }]
  };
  private changValue(text: string) {
    this.ruleForm.content = text;
  }
  private changMark(text: string) {
    this.ruleForm.content_mark = text;
  }
  private submitForm(formName: string) {
    (this.$refs[formName] as any).validate((valid: boolean) => {
      if (valid) {
        console.log(this.ruleForm);
        (this as any).$axios
          .post(`${apiUrl.articleAdd}`, this.ruleForm)
          .then((res: any) => {
            if (res.data.code == 0) {
              this.$router.push("/article/list");
              this.$message.success("发表成功");
              (this as any).resetForm("ruleForm");
            }
          });
      } else {
        console.log("error submit!!");
        return false;
      }
    });
  }
  private resetForm(formName: string) {
    (this.$refs[formName] as any).resetFields();
    (this.$refs.markdown as any).value = "";
  }
  created() {
    this.ruleForm.article_author = this.getArticleName;
    (this as any).$axios.get(`${apiUrl.tagList}`).then((res: any) => {
      if (res.data.code === 0) {
        this.categoryList = res.data.message;
        console.log(res);
      }
    });
  }
  private changeUrl(img: string) {
    this.ruleForm.thumb_url = img;
  }
  private removeUrl() {
    this.ruleForm.thumb_url = "";
  }
}
